import React, { Component } from 'react'
import { withRouter } from 'next/router'
import Layout from '../components/MyLayout'

let infos = {}

class Page extends Component {

  constructor(props) {
    super(props)
    this.state = {
      infos: []
    }
  }

  componentWillMount(props) {
    this.props = props;

    withRouter(props => {
      fetch('http://assistenciafarmaceutica.far.br/wp-json/wp/v2/posts/' + props.router.query.id, {
        method: 'GET'
      })
        .then(response =>
          response.json().then(data => {

            this.state.infos = [...this.state.infos, [data.title.rendered, data.content.rendered, data.date]]

            console.log(data)
          }))
        .catch(function (err) {
          console.error(err);
        });
    })
  }

  render(props) {
    return (
      <Layout>
        <h1>{infos.title}</h1>
        <p>This is the Blog Post Content! ;-P</p>
      </Layout>
    )
  }
}

export default Page